import React, { useState } from "react";

/* Style */
import {
    Container,
    BoxBorder,
    BoxContainer,
    SmallSpan,
    TitleContainer,
    IdeaContainer,
    ContainerImage,
    LanguageContainer,
    EachProgress,
    Languages,
    LanguagesContainer,
    TechnologiesContainer,
    ContainerTechImage
} from "./experience.styles";

/* Material UI */
import Typography from "@material-ui/core/Typography";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import makeStyles from "@material-ui/core/styles/makeStyles";
import LinearProgress from '@material-ui/core/LinearProgress';
import Box from '@material-ui/core/Box';
import Chip from '@material-ui/core/Chip';
import { Theme } from "@material-ui/core";
import Button from '@material-ui/core/Button';

/* Translation */
import { useTranslation } from "react-i18next";
import withStyles from "@material-ui/core/styles/withStyles";

/* Icons */
import { ReactComponent as Lamp } from "./../../assets/lamp.svg";
import { ReactComponent as LightLamp } from "./../../assets/light-lamp.svg";
import { ReactComponent as US } from "./../../assets/united-states.svg";
import { ReactComponent as BR } from "./../../assets/brasil.svg";

/* Icons about project technologies */
import { ReactComponent as MySQL } from "./../../assets/technologies/MySQL-Logo.svg";

import {dkItems, cmItems, english, frontendItems, backendItems} from "./constants";

type Props = {
    value: number;
};

const AboutTypography = withStyles({
    root: {
        color: "#e0e0e0",
        marginTop: 20
    }
})(Typography);

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'relative',
        width: 200,
        display: "flex",
        justifyContent: "flex-end"
    },
    dropdown: {
        position: 'absolute',
        top: 60,
        right: 0,
        left: 0,
        zIndex: 1,
        border: '1px solid',
        padding: theme.spacing(1),
        backgroundColor: theme.palette.background.paper,
        color: "#424242",
    },
    rootChip: {
        display: 'flex',
        marginBottom: 20,
        flexWrap: 'wrap',
        '& > *': {
            margin: theme.spacing(0.5),
        },
    },
}));

const useStylesProgress = makeStyles<Theme, Props>((theme) => ({
    root: {
        height: 5,
        borderRadius: 5
    },
    bar: ({ value }) => ({
        background: `linear-gradient(90deg, #6fcbb6 ${100 - value}%, #853ff1 100%)`
    })
}));

function LinearProgressWithLabel({ value }: Props) {
    const classes = useStylesProgress({ value });

    return (
        <Box display="flex" alignItems="center">
            <Box width="100%" mr={1}>
                <LinearProgress classes={{ root: classes.root, bar: classes.bar }} color="secondary" variant="determinate" value={value} />
            </Box>
            <Box color="white" minWidth={35}>
                <Typography variant="body2">{`${Math.round(
                    value,
                )}%`}
                </Typography>
            </Box>
        </Box>
    );
}

const Experience: React.FC = () => {
    const { t } = useTranslation();
    const [lampStatusDK, setLampStatusDK] = useState(false);
    const [lampStatusCM, setLampStatusCM] = useState(false);

    const [showTechnologies, setShowTechnologies] = useState(false);

    const classes = useStyles();

    const handleClickAwayDK = () => {
        setLampStatusDK(false);
    };

    const handleClickAwayCM = () => {
        setLampStatusCM(false);
    };

    return (
        <>
            <Container onMouseLeave={() => {
                setLampStatusDK(false);
                setLampStatusCM(false);
            }}>
                <Typography variant="h4" style={{ marginBottom: 20 }}>
                    <span style={{ color: "#FB87FD" }}>{t("experience-skills")}</span>
                </Typography>
                <BoxContainer onMouseLeave={() => {
                    setLampStatusDK(false);
                    setLampStatusCM(false);
                }}>
                    <BoxBorder>
                        <TitleContainer>
                            <Typography variant="h5" style={{ marginBottom: 20 }}>
                                {t("frontend-developer")}
                            </Typography>

                            {lampStatusDK ?
                                <>
                                    <ClickAwayListener
                                        mouseEvent="onMouseDown"
                                        touchEvent="onTouchStart"
                                        onClickAway={handleClickAwayDK}
                                    >
                                        <div className={classes.root}>
                                            <LightLamp style={{ width: 50, height: 50 }}
                                                onMouseLeave={() => setLampStatusDK(false)} />
                                            <div className={classes.dropdown}>
                                                {dkItems.map((element, index) => {
                                                    return (
                                                        <ContainerImage key={index}>
                                                            <img alt={element.label} style={{ width: element.width, height: element.height, paddingRight: element.right, paddingLeft: element.left }} src={element.image} />
                                                            <div>{element.label}</div>
                                                        </ContainerImage>)
                                                })}
                                            </div>
                                        </div>
                                    </ClickAwayListener>
                                </> :
                                <IdeaContainer onMouseEnter={() => setLampStatusDK(true)}>
                                    <Lamp style={{ width: 40, height: 40, fill: "#e0e0e0", marginBottom: 5 }} />
                                    <SmallSpan> + info</SmallSpan>
                                </IdeaContainer>}
                        </TitleContainer>
                        <Typography variant="body1" style={{ marginBottom: 20 }}>
                            Drakkar <SmallSpan>({t("internship")})</SmallSpan>
                        </Typography>
                        <div className={classes.rootChip}>
                            <Chip style={{ backgroundColor: "#FB87FD", fontWeight: "bold" }} size="small" label="Frontend" />
                            <Chip style={{ backgroundColor: "pink", fontWeight: "bold" }} size="small" label="Design" />
                        </div>
                        <SmallSpan>
                            {t("drakkar-period")}
                        </SmallSpan>
                        <AboutTypography variant="body1">
                            {t("about-drakkar")}
                        </AboutTypography>
                    </BoxBorder>

                    <BoxBorder>
                        <TitleContainer>
                            <Typography variant="h5" style={{ marginBottom: 20 }}>
                                {t("frontend-developer")}
                            </Typography>
                            {lampStatusCM ?
                                <>
                                    <ClickAwayListener
                                        mouseEvent="onMouseDown"
                                        touchEvent="onTouchStart"
                                        onClickAway={handleClickAwayCM}
                                    >
                                        <div className={classes.root}>
                                            <LightLamp style={{ width: 50, height: 50 }}
                                                onMouseLeave={() => setLampStatusCM(false)} />
                                            <div className={classes.dropdown}>
                                                {cmItems.map((element, index) => {
                                                    return (
                                                        <ContainerImage key={index}>
                                                            <img alt={element.label} style={{ width: element.width, height: element.height, paddingRight: element.right, paddingLeft: element.left }} src={element.image} />
                                                            <div>{element.label}</div>
                                                        </ContainerImage>)
                                                })}
                                            </div>
                                        </div>
                                    </ClickAwayListener>
                                </> :
                                <IdeaContainer onMouseEnter={() => setLampStatusCM(true)}>
                                    <Lamp style={{ width: 40, height: 40, fill: "#e0e0e0", marginBottom: 5 }} />
                                    <SmallSpan> + info</SmallSpan>
                                </IdeaContainer>}
                        </TitleContainer>
                        <Typography variant="body1" style={{ marginBottom: 20 }}>
                            COWMED <SmallSpan>({t("internship")})</SmallSpan>
                        </Typography>
                        <div className={classes.rootChip}>
                            <Chip style={{ backgroundColor: "#FB87FD", fontWeight: "bold" }} size="small" label="Frontend" />
                            <Chip style={{ backgroundColor: "pink", fontWeight: "bold" }} size="small" label="Design" />
                        </div>
                        <SmallSpan>
                            {t("cowmed-period")}
                        </SmallSpan>
                        <AboutTypography variant="body1">
                            {t("about-cowmed")}
                        </AboutTypography>
                    </BoxBorder>
                </BoxContainer>
                <LanguagesContainer>
                    <Typography variant="h5" style={{ marginBottom: 30 }}>
                        {t("languages")}
                    </Typography>
                    <Languages>
                        <LanguageContainer>
                            <US style={{ width: 50, height: 50, margin: "0 0 30px 0" }} />
                            {english.map((element, index) => {
                                return (
                                    <EachProgress key={index}>
                                        <Typography variant="h6" style={{ marginBottom: element.bottom }}>
                                            {t(element.translation)}
                                        </Typography>
                                        <LinearProgressWithLabel value={element.value} />
                                    </EachProgress>
                                )
                            })}
                        </LanguageContainer>
                        <LanguageContainer>
                            <BR style={{ width: 50, height: 50, margin: "0 0 30px 0" }} />
                            {english.map((element, index) => {
                                return (
                                    <EachProgress key={index}>
                                        <Typography variant="h6" style={{ marginBottom: element.bottom }}>
                                            {t(element.translation)}
                                        </Typography>
                                        <LinearProgressWithLabel value={100} />
                                    </EachProgress>
                                )
                            })}
                        </LanguageContainer>
                    </Languages>

                </LanguagesContainer>
                <LanguagesContainer>
                    <Typography variant="h5" style={{ marginBottom: 30 }}>
                        {t("all-technologies")}
                    </Typography>
                    <Button variant="contained" style={{ backgroundColor: "#1f1c21", border: "1px solid #B485FF" }} onClick={() => { setShowTechnologies(!showTechnologies) }} color="secondary">
                        <LightLamp style={{ width: 40, height: 40, marginRight: 20 }}
                            onMouseLeave={() => setLampStatusCM(false)} /> {showTechnologies ? t("hide") : t("show")}
                    </Button>
                    {showTechnologies ?
                        <BoxBorder style={{ maxWidth: 500 }}>
                            <Typography variant="h5" style={{ marginBottom: 20 }}>
                                Frontend
                            </Typography>
                            <TechnologiesContainer>
                                {frontendItems.map((element, index) => {
                                    return (
                                        <ContainerTechImage key={index}>
                                            <img alt={element.label} style={{ width: element.width, height: element.height, paddingRight: element.right }}
                                                src={element.image} />
                                            <div>{element.label}</div>
                                        </ContainerTechImage>
                                    )
                                })}
                            </TechnologiesContainer>
                            <Typography variant="h5" style={{ marginBottom: 20 }}>
                                Backend
                            </Typography>
                            <TechnologiesContainer>
                                {backendItems.map((element, index) => {
                                    return (
                                        <ContainerTechImage key={index}>
                                            <img alt={element.label} style={{
                                                width: element.width,
                                                height: element.height,
                                                marginRight: element.right,
                                                marginLeft: element.left,
                                                backgroundColor: element?.background
                                            }} src={element.image} />
                                            <div>{element.label}</div>
                                        </ContainerTechImage>
                                    )
                                })}
                                <ContainerTechImage>
                                    <MySQL style={{
                                        width: 52,
                                        height: 50,
                                        marginRight: 5,
                                    }} />
                                    <div>MySQL</div>
                                </ContainerTechImage>
                            </TechnologiesContainer>
                        </BoxBorder>
                        : null}
                </LanguagesContainer>
            </Container>
        </>
    )
}

export default Experience;